<template>
  <div class="zk-main">
    <div class="button-group">
      <span class="route-name">{{$route.meta.title}}</span>
      <el-button icon="el-icon-plus" size="small" type="warning" @click="modifyCluster()">{{$t('common.add')}}</el-button>
    </div>
    <el-form :model="clusterForm" ref="clusterForm" label-width="85px" label-position="right" size="small">
      <el-row class="label-first" :gutter="20">
        <el-col :lg="6" :xl="4">
          <el-form-item label="集群名称：">
            <el-input v-model.trim="clusterForm.name"></el-input>
          </el-form-item>
        </el-col>
        <el-col :lg="6" :xl="4">
          <el-button type="primary" icon="el-icon-search" @click="searchCluster" size="small">{{$t('common.search')}}</el-button>
        </el-col>
      </el-row>
    </el-form>
    <el-table v-loading="tableLoading" :data="clusterInfo.list" stripe>
      <el-table-column
        prop="name"
        align="center"
        label="集群名称"
        width="300">
      </el-table-column>
      <el-table-column
        label="集群详情"
        width="150px"
        align="center">
        <template slot-scope="props">
          <router-link :to="{name: 'zkList', params: props.row}">
            <i class="fa fa-cubes font-16 pointer"></i>
          </router-link>
        </template>
      </el-table-column>
      <el-table-column
        prop="remark"
        align="center"
        label="备注">
      </el-table-column>
      <el-table-column
        width="200"
        align="center"
        :label="$t('common.operation')">
        <template slot-scope="props">
          <el-button type="text" size="mini" icon="el-icon-edit" @click="modifyCluster(props.row)">{{$t('common.modify') }}</el-button>
          <el-button
            type="text"
            class="btn-text-red"
            size="mini"
            icon="el-icon-delete"
            @click="deleteCluster(props.row)">
            {{$t('common.delete')}}
          </el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-pagination
      background
      @current-change="handleCurrentChange"
      :current-page.sync="currentPage"
      :page-size="pageSize"
      layout="total, prev, pager, next, jumper"
      :total="clusterInfo.total">
    </el-pagination>
    <!-- 添加集群 -->
    <el-dialog
      :title="title"
      :visible.sync="clusterVisible"
      width="30%"
      @close="clusterCancel"
      center>
      <el-form :model="clusterAlertForm" :rules="clusterAlertRules" ref="clusterAlertForm" label-width="80px" size="small">
        <el-form-item label="集群名称" prop="name">
          <el-input v-model.trim="clusterAlertForm.name" maxlength="50"></el-input>
        </el-form-item>
        <el-form-item label="描述" prop="remark">
          <el-input type="textarea" maxlength="100" v-model.trim="clusterAlertForm.remark"></el-input>
          <div class="remark-length">{{clusterAlertForm.remark ? clusterAlertForm.remark.length : '0'}}/100</div>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="clusterOk('clusterAlertForm')" size="small">{{ $t('common.ok') }}</el-button>
        <el-button @click="clusterCancel" size="small">{{ $t('common.cancel') }}</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
  import {mapState, mapActions} from 'vuex'
  import Api from '../../constants/Api'

  export default {
    data() {
      return {
        currentPage: 1,
        pageSize: 10,
        clusterVisible: false,
        clusterForm: {
          name: ''
        },
        title: '',
        clusterAlertForm: {},
        clusterAlertRules: {
          name: [
            {required: true, message: '请输入集群名称', trigger: 'blur'}
          ]
        },
        tableLoading: false
      }
    },
    computed: {
      ...mapState({
        clusterInfo: state => state.zookeeper.clusterInfo
      })
    },
    created() {
      this.getTableData()
    },
    methods: {
      ...mapActions('zookeeper', ['getClusterList', 'operationTable']),
      searchCluster() {
        this.getTableData()
      },
      async getTableData() {
        const vm = this
        const params = {
          start: vm.currentPage,
          length: vm.pageSize
        }
        if (vm.clusterForm.name) {
          params.name = vm.clusterForm.name
        }
        vm.tableLoading = true
        await vm.getClusterList(params)
        vm.tableLoading = false
      },
      deleteCluster(props) {
        const vm = this
        vm.$confirm(`是否确认删除集群${props.name}？`, '删除', {
          confirmButtonText: vm.$t('common.ok'),
          cancelButtonText: vm.$t('common.cancel'),
          type: 'warning',
          center: true
        }).then(() => {
          vm.currentPage = 1
          vm.operationTable({
            url: Api.zkClusterDelete,
            data: { id: props.id },
            callback: () => {
              vm.$message({
                type: 'success',
                message: '删除成功'
              })
              vm.currentPage = 1
              vm.getTableData()
            },
            callbackError: (error) => {
              vm.$message({
                type: 'error',
                message: (error.data && error.data.message) || '删除失败'
              })
            }
          })
        }).catch(() => {})
      },
      modifyCluster(cluster) {
        const vm = this
        if (cluster) {
          vm.title = '修改zk集群'
          vm.clusterAlertForm = {...cluster}
          vm.clusterVisible = true
        } else {
          vm.title = '添加zk集群'
          vm.clusterAlertForm = {}
          vm.clusterVisible = true
        }
      },
      handleCurrentChange(val) {
        this.currentPage = val
        this.getTableData()
      },
      clusterCancel() {
        const vm = this
        vm.clusterVisible = false
        vm.$refs['clusterAlertForm'].clearValidate()
      },
      clusterOk(formName) {
        const vm = this
        vm.$refs[formName].validate(valid => {
          if (valid) {
            if (vm.clusterAlertForm.id) {
              vm.operationTable({
                url: Api.zkClusterUpdate,
                data: vm.clusterAlertForm,
                callback: () => {
                  vm.$message({
                    type: 'success',
                    message: '修改成功'
                  })
                  vm.clusterCancel()
                  vm.getTableData()
                },
                callbackError: (error) => {
                  vm.$message({
                    type: 'error',
                    message: (error.data && error.data.message) || '修改失败'
                  })
                }
              })
            } else {
              vm.operationTable({
                url: Api.zkClusterAdd,
                data: vm.clusterAlertForm,
                callback: () => {
                  vm.$message({
                    type: 'success',
                    message: '添加成功'
                  })
                  vm.clusterCancel()
                  vm.getTableData()
                },
                callbackError: (error) => {
                  vm.$message({
                    type: 'error',
                    message: (error.data && error.data.message) || '添加失败'
                  })
                }
              })
            }
          }
        })
      }
    }
  }
</script>

<style lang="scss" scoped>
  @import "../../assets/common.scss";
  .remark-length {
    color: #999;
    text-align: right;
  }
</style>



